      "message": "回想自己的高中三年，有欢喜有忧愁。高一高二是懵懂的年纪，高三才知道自己背负的是什么。我忘记不了高三的奋笔疾书，忘不了高三因为考的差而彻夜难眠，同样也忘不了高一高二的无忧无虑。祝高考的你们一切顺利，愿你们以梦为马，不负韶华。高考只是一个开始，未来还长，愿你们永远心怀梦想，拥抱阳光。",
